/* frontend/src/App.css */

@import 'prismjs/themes/prism.css';

/* frontend/src/App.css */

.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.App-header {
  background-color: #282c34;
  padding: 20px;
  color: white;
}

main {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #f0f0f0;
  overflow-y: auto;
  padding: 20px;
}

textarea {
  width: 80%;
  max-width: 600px;
  padding: 10px;
  margin-top: 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
  font-size: 16px;
}

button {
  margin-top: 10px;
  padding: 10px 20px;
  font-size: 16px;
  border: none;
  border-radius: 5px;
  background-color: #61dafb;
  cursor: pointer;
}

button:hover {
  background-color: #21a1f1;
}

.error {
  color: red;
  margin-top: 10px;
}

/* Styling for the code display */
pre {
  background: #2d2d2d;
  color: white;
  padding: 15px;
  border-radius: 5px;
  width: 80%;
  max-width: 800px;
  overflow-x: auto;
  margin-top: 20px;
}

code {
  font-family: 'Source Code Pro', monospace;
  font-size: 14px;
}
